@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
html[data-theme='dark'] {
  --color: rgb(250, 250, 250);
  --background-color: rgb(5, 5, 5);
  --secondary-color: rgb(250, 250, 250);
}
html[data-theme='light'] {
  --color: rgb(5, 5, 5);
  --background-color: rgb(250, 250, 250);
  --secondary-color: rgb(5, 5, 5);
}

* {
  font-size: 1em;
  background: var(--background-color);
  color: var(--color);
  font-family: 'Press Start 2P', cursive;
  transition: background-color 0.3s ease-out, color 0.3s ease-out;
}

body {
  min-height: 100vh;
  min-height: -webkit-fill-available;
}

.landing-choose-game{position:absolute;left:50%;top:50%;transform:translateX(-50%);display:flex;flex-direction:column}.landing-choose-game-button{border:2px solid var(--secondary-color);text-align:center;text-decoration:none;margin:1em;border-radius:10px;padding:1em;width:15em}.landing-footer{position:absolute;left:0;width:100%;bottom:3em;display:flex;justify-content:space-around;margin:0;padding:0}.landing-footer-button{border:2px solid var(--secondary-color);align-self:center;border-radius:10px;padding:1em}.landing-about{position:absolute;width:100%;height:100%;top:105%;z-index:10;transition:.5s}.landing-about-expanded{position:absolute;width:100%;height:100%;top:0%;z-index:10;transition:.7s}
.toggle-switch{position:absolute;display:block;width:60px;height:30px}.toggle-switch input{display:none}.slider{position:absolute;top:0;left:0;right:0;bottom:0;background:var(--secondary-color);transition:.3s}.slider:before{position:absolute;content:"";left:3px;top:3px;background:var(--background-color);transition:.3s}input:checked+.slider{background-color:var(--secondary-color)}input:checked+.slider:before{transform:translateX(30px);background:var(--background-color)}.slider{border-radius:40px}.slider:before{width:40%;height:80%;border-radius:50%}
.modal-overlay{position:fixed;top:0;left:0;z-index:1040;width:100vw;height:100vh;background:var(--fade);opacity:.3}.modal-wrapper{position:fixed;top:0;left:0;z-index:1050;width:100%;height:100%;overflow-x:hidden;overflow-y:auto;outline:0}.modal{z-index:100;background:var(--background-color);position:relative;margin:1.75rem auto;border-radius:3px;padding:2rem}.modal-close-button{position:absolute;right:1em;top:1em;font-size:1.4rem;font-weight:700;line-height:1;color:var(--color);border:none}
.form-text-input{text-align:center;margin-top:.5em;padding:.5em;border-radius:10px;border:1px solid gray;max-width:100%}
